import React, { useState } from 'react';
import { Layout } from 'components/layout/user';
import axios from 'axios';

const GoodsPresenter = () => {
  const [goods, setGoods] = useState([]);
  const [search, setSearch] = useState('');
  const handleClickGoods = async() => {
    // const data = {
    //   "startDate": "2017-08-01",
    //   "endDate": "2017-09-30",
    //   "timeUnit": "month",
    //   "category": [
    //       {"name": "", "param": ["50000000"]},
    //       {"name": "/", "param": ["50000002"]}
    //   ],
    //   "device": "pc",
    //   "ages": ["20", "30"],
    //   "gender": "f"
    // }
    // const config = {
    //     headers: {
    //         "X-Naver-Client-Id": 'YxD7HKQsx5SslP1h4zau',
    //         "X-Naver-Client-Secret": 'wucpAPCoLg',
    //         "Content-Type": "application/json"
    //     }
    // };
    await axios.get(`/api/test/naverapi/${search}`).then((response)=>{
      console.log(response)
      setGoods(response.data.items)
    });
  }
  return (
    <Layout page="goods">
      <div>
        <input type='text' style={{marginLeft: '10px'}} value={search} onChange={(e)=>setSearch(e.target.value)}/><button style={{marginLeft: '10px'}} onClick={handleClickGoods}></button>
      </div>
      {goods.map((good, i) => (
        <div style={{width: '100%'}} key={i}>
          <div style={{ padding: '10px'}}>
              <div style={{boxShadow: '0px 1px 5px rgba(0, 0, 0, 0.15)', borderRadius: '10px'}}>
                <img src={good.image} style={{width: '100%', borderBottom: '1px solid rgba(0, 0, 0, 0.1)'}}/>
                <div style={{padding: '10px'}}>
                  <p> ID : {good.productId}</p>
                  <p>  : {good.title.split('<b>').join('').split('</b>').join('')}</p>
                  <p> : {good.productType}</p>
                  <p> : {good.category1} &gt; {good.category2} &gt; {good.category3} &gt; {good.category4}</p>
                  <p> : {good.lprice}</p>
                  <p> : {good.hprice}</p>
                  <p>  : {good.brand}</p>
                  <p>  : {good.maker}</p>
                  <p>Link : <a href={good.link} target="_blank"></a></p>
                </div>
              </div>
          </div>
        </div>
      ))}
    </Layout>
  ); 
};

export default GoodsPresenter;
